// import axios from 'axios';
import React, { Component, lazy, Suspense } from "react";
import api from "../ApiServise/Api";
import { Route, Switch, NavLink, withRouter} from "react-router-dom";
import MovieDetails from '../Components/MovieDetails/MovieDetails';
import Loader from "../data/Loader";
import routes from "../routes";
import s from "../Styles/styles.module.css";

const Cast = lazy(() =>
  import("../Components/Cast/Cast" /* webpackChunkName: "Cast" */ )
);
const Review = lazy(() =>
  import("../Components/Review/Review" /* webpackChunkName: "Review" */ )
);

class MovieDetailsPage extends Component {
  state = {
    movieObj: {},
    // genres: [],
  };

  async componentDidMount() {
    const { movieId } = this.props.match.params;
    console.log(movieId)
    const result = await api.getMovieDetails(movieId);
    this.setState({ movieObj: result });
    console.log(result);
  };

  handleGoBack = () => {
    const { location, history } = this.props;
    history.push(location?.state?.from || routes.moviesPage);
  };

  render() {
    const { match, location } = this.props;
    const { movieObj, genres } = this.state;
    const { id } = movieObj;
    // console.log(id);
    // console.log(movieObj);
    // console.log(genres)
     return (
      <>
        <div>        
          <button type="button" className={s.btn} onClick={this.handleGoBack}>
            Go Back
          </button>
          <div> 
            {/* {movieObj.map((movie) => ( */}
            {/* <MovieDetails movieObj={movieObj} key={id} /> */}
            <MovieDetails movieObj={movieObj} key={id} />

            {/* ))} */}
          </div>
        </div>

        <section>
          <div className={s.moreInfo}>
            <h3 className={s.info}>More information</h3>
            <ul className={s.link}>
              <li>
                <NavLink
                  to={{
                    pathname: `${match.url}/cast`,
                    state: { from: location?.state?.from },
                  }}
                  className={s.link}
                >
                  Cast
                </NavLink>
              </li>
              <li>
                <NavLink
                  to={{
                    pathname: `${match.url}/reviews`,
                    state: { from: location?.state?.from,},
                  }}
                  className={s.link}
                >
                  Reviews
                </NavLink>
              </li>
            </ul>
          </div>
          <Suspense fallback={ <div className={s.loaderContainer}><Loader /></div> }>
            <Switch>
              <Route path={`${match.url}/cast`} componen={Cast} />
              {/* <Cast movieId={id} /> */}
              <Route path={`${match.url}/review`} componen={Review} />
              {/* <Reviews movieId={id} /> */}
            </Switch>
          </Suspense>
        </section>
      </>
    );
  }
}

export default withRouter(MovieDetailsPage);
